.restaurants-container {
  text-align: center;
  padding: 20px;
}

.restaurants-title {
  font-size: 2rem;
  margin-bottom: 20px;
}

.search-bar {
  width: 50%;
  padding: 10px;
  margin-bottom: 30px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
}

.restaurants-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
}

.restaurant-card {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease;
}

.restaurant-card:hover {
  transform: translateY(-5px);
}

.restaurant-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
}

.restaurant-name {
  font-size: 1.2rem;
  font-weight: bold;
  margin: 10px 0;
}

.restaurant-location,
.restaurant-rating {
  font-size: 1rem;
  color: #555;
}
