/* Overall container */
.home-container {
    text-align: center;
    padding: 20px;
    background-color: #e3f2fd;
    min-height: 100vh;
    color: #1976d2;
}

/* Title styling */
.home-title {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 20px;
}

/* Paragraph styling */
.home-description {
    font-size: 20px;
    margin-bottom: 30px;
}

/* Navigation buttons container */
.home-nav {
    margin-top: 30px;
}

/* Button styling */
.home-button {
    background-color: #fff;
    color: #1976d2;
    padding: 10px 20px;
    border: 2px solid #1976d2;
    border-radius: 8px;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    transition: all 0.3s ease;
    cursor: pointer;
    margin: 0 10px;
}

.home-button:hover {
    background-color: #1976d2;
    color: #fff;
}

/* Why Us Section */
.why-us-section {
    background-color: #ffffff;
    margin-top: 30px;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Why Us Title */
.why-us-title {
    font-size: 28px;
    font-weight: bold;
    color: #0d47a1;
    text-align: left;
    margin: 0;
    padding-bottom: 10px;
}

/* Why Us Description */
.why-us-description {
    font-size: 18px;
    line-height: 1.6;
    text-align: justify;
    color: #1976d2;
}

/* Images grid container */
.home-images-grid {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-top: 30px;
    padding: 0 20px; /* Add padding to the sides for spacing */
}

/* Individual image container */
.image-container {
    text-align: center;
    flex: 1; /* Make each image container take up equal space */
}

/* Individual image styling */
.home-image {
    width: 100%;
    height: auto;
    border-radius: 12px;
    object-fit: cover;
}

/* Image description */
.image-description {
    font-size: 18px;
    color: #1976d2;
    margin-top: 10px;
    font-weight: bold;
}
