.weather-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #e8f4fd; /* Light blue background */
    color: #007bff; /* Primary blue for text */
}

.weather-title {
    font-size: 2.5rem;
    margin-bottom: 20px;
    font-weight: bold;
    color: #1976d2; /* Primary blue */
}

.weather-input {
    display: flex;
    gap: 10px;
    margin-bottom: 30px;
}

.weather-input input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
    outline: none;
}

.weather-input button {
    background-color: #fff;
    color: #1976d2;
    padding: 10px 20px;
    border: 2px solid #1976d2;
    border-radius: 8px;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    transition: all 0.3s ease;
    cursor: pointer;
    margin: 0 10px;
}

.weather-input button:hover {
    background-color: #0056b3; /* Darker blue on hover */
}

.weather-result {
    background-color: white;
    color: #1976d2;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    max-width: 400px;
    position: relative;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Light shadow for depth */
    margin-top: 20px;
}

.weather-result h2 {
    font-size: 1.8rem;
    margin-bottom: 15px;
}

.weather-result p {
    margin: 5px 0;
}

.weather-image {
    width: 200px; /* Larger image */
    height: auto;
    margin-top: 20px;
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Light shadow */
}
